import { Component, h, Host, Prop } from '@stencil/core';
import type { Link } from '../global/shared-types/link.types';

/**
 * @private
 */

@Component({
    tag: 'limel-whats-new-card',
    shadow: true,
    styleUrl: 'whats-new-card.scss',
})
export class WhatsNewCard {
    @Prop()
    public heading: string;

    @Prop()
    public subheading: string;

    @Prop({ reflect: true })
    public type: 'feature' | 'component' = 'feature';

    @Prop()
    public description: string;

    @Prop()
    public link: Link;

    public render() {
        return (
            <Host>
                <h2>{this.heading}</h2>
                <h3>{this.subheading}</h3>
                <div class="news-card-content">
                    <slot name="demo" />
                </div>
                {this.renderBadge()}
                <limel-markdown value={this.description} />
                {this.renderLink()}
            </Host>
        );
    }

    private renderBadge() {
        if (this.type === 'feature') {
            return <limel-badge label="Feature" />;
        }
        return <limel-badge label="Component" />;
    }

    private renderLink() {
        return (
            <limel-chip text={`Go to ${this.subheading}`} link={this.link} />
        );
    }
}
