*,
:host(limel-whats-new-card) {
    box-sizing: border-box;
    min-width: 0;
    min-height: 0;
}

:host(limel-whats-new-card) {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    align-items: flex-start;
    width: auto;
    height: auto;
    border-radius: 0.95rem;
    border: 1px solid rgb(var(--contrast-500));
    padding: 1rem 0.75rem;
    background-color: rgb(var(--contrast-200));
    box-shadow: var(--shadow-depth-8);
    --badge-max-width: auto;
}

:host(limel-whats-new-card[type='feature']) {
    --badge-background-color: rgb(var(--color-lime-lighter));
}

:host(limel-whats-new-card[type='component']) {
    --badge-background-color: rgb(var(--color-amber-lighter));
}

.news-chip-wrapper {
    display: flex;
    gap: 0.5rem;
    margin: 1rem 0;
}

limel-chip {
    --chip-readonly-border-color: rgb(var(--color-green-default));
}

.news-card-content {
    width: 100%;
    padding: 1rem;
    background-color: rgb(var(--contrast-400));
    border-radius: 0.95rem;
}

h2,
h3 {
    margin: 0;
}

h2 {
    font-size: 1.125rem;
    font-weight: 500;
    color: var(--card-heading-color, rgb(var(--contrast-1100)));
    letter-spacing: -0.03125rem;
}

h3 {
    font-size: 0.75rem;
    font-weight: 500;
    color: var(--card-heading-color, rgb(var(--contrast-800)));
}
